import axios from 'axios';
import store from '@/store';
import { Loading } from 'element-ui'
const service = axios.create({
    // baseURL: process.env.VUE_APP_BASE_API,
    loadingStatus: false             // 
});
let loadingInstance = null;
let loadEntity = {
  lock: true,
  text: '',    // lang.LOADING,
  spinner: 'el-icon-loading',
  background: 'rgba(0, 0, 0, 0.3)',
  customClass: 'loading'
}
let needLoadingRequestCount = 0;
service.interceptors.request.use(
    config => {
       config.headers['Access-Token'] = store.state.auth.token; // token
       config.headers['X-Requested-With']  = 'XMLHttpRequest';
       if (config.loadingStatus) {
           needLoadingRequestCount++;
           loadingInstance = Loading.service(loadEntity);
       }

       return config;
    }, error => {
        promise.reject(error);
    }
);

// respone 
service.interceptors.response.use(
    response => {
      if (response.config.loadingStatus) {
          needLoadingRequestCount--;
          if (needLoadingRequestCount <= 0) {
              loadingInstance.close();
          }
      }
      return response.data;
    },
    error => {
        if (error.response.config.loadingStatus) {
            needLoadingRequestCount--;
            if (needLoadingRequestCount <= 0) {
                loadingInstance.close();
            }
        }
        if (error && error.response && error.response.status === 401) {
            let href = null;
            try {
                href = parent.location.href;
            } catch (e) {
                href = document.referrer;
            }
            let index = href && href.indexOf('?token=');
            if (index > 0) {
                href = href.substr(0, index);
            }
            let loginurl = error.response.data.sso_url + href;
            parent.location.href = loginurl;
        }
        return  error.response;
    }
);
export default service;
